// Prevents font sizes changes when in orientation mode
html {-webkit-text-size-adjust: 100%; -ms-text-size-adjust: 100%;}
// Apply box-sizing to all elements
* { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

body {
    font-family: $defaultFont;
    font-size: 16px;
}
//restricting to a max of 1200px
header, nav, main, footer {
    max-width: 75rem;
    margin: auto;
}
#headWrapper { //header background color with border
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(2,0,36,1) 100%);
    border-bottom: 5px solid $primaryColor;
} 
header {
display: flex;
color: white;
letter-spacing: .1rem;
img { //logo sizing
    height: 100%;
    width: auto;
}  
h1 {
    font-size: $h1size;
    font-weight: 700;
    padding-top: 2.7rem; //changes the padding to center vertically without slogan
} 
h2 {
    display: none; //disspears on small screens
    font-style: italic;
    padding-top: .5rem;
}

}
.navWrapper { //adds border that matches header border
    border-bottom: 5px solid $primaryColor;
}
nav { //small veritcal navigation
    ul {
        li{
            padding: 1rem;
            border: 5px solid;
            border-image-slice: 1;
            border-image-source: 
            linear-gradient(to bottom, $primaryColor, black); //border only for small screens
            font-weight: 700;
            a { //resetting links
                text-decoration: none;
                color: black;
                display: block;
            }
        }
        li.active { //wayfinding for current page
            background: $secondaryColor;
            a {
                color: white;
            }
        }
        li:hover { //wayfinding
            background: $secondaryColor;
            a {
                color: white;
            }
        }
    }
}

#mainWrapper { //background for main page
    background: black;
}

main {
    background: white;
    padding-bottom: 6rem; //adding space to bottom of text and figure
    h1 {
        background: $primaryColor;
        padding: 2rem 1rem;
        font-size: $h2size; 
        color: white;
        position: relative; //moving the page title up over the image slightly
        bottom: 20px;
    }
    figure { //scaling figure and image of nucleus
        float: right;
        width: 100%;
        height: auto;
        padding: 1rem;
        img {
           width: 100%;
        }
        figcaption {
            padding: 1rem;
            text-align: center;
            font-style: italic;
        }
    }
    p {
        padding: 0 1rem;
        line-height: 1.3rem; //making text more readable
    }
    img { //hero image scaling
        width: 100%;
        height: auto;
    }
}
#footerWrapper {
    background: $primaryColor;
}
footer {
    padding: 2rem;
    color: white;
    text-align: center;
}
